<template>
	<view>
		<button @click="get">发送</button>
		<button type="primary" @click="setStorage">数据储存</button>
		<button type="primary" @click="getStorage">获取数据</button>
		<button type="primary" @click="removeId">移除ID</button>
		<view class="bos-item" v-for="item in list">
			{{item}}
		</view>
	</view>
</template>

<script>
	export default {
		data(){
			return{
				list:['前端','JAVA','UI','测试','大数据','JAVA','UI','测试','大数据']
			}
		},
		onPullDownRefresh() {
			console.log('出发了下拉刷新')
			setTimeout(()=>{
				this.list = ['JAVA','UI','测试','大数据','前端','JAVA','UI','测试','大数据']
				uni.stopPullDownRefresh()
			},2000)
		},
		onReachBottom() {
			console.log('到底啦')
			this.list = [...this.list,...['前端','JAVA','UI','测试','大数据','JAVA','UI','测试','大数据']]
		},
		methods:{
			get(){
				uni.request({
					url:"http://localhost:8082/api/getlunbo",
					success(res){
						console.log(res)
					}
				})
			},
			setStorage(){
			/* 	uni.setStorage({
					key:'id',
					data:80,
					success(){
						console.log('存储成功')
					}
				}) */
				//两种方法
				uni.setStorageSync('id',100)
			},
			getStorage(){
				/* uni.getStorage({
					key:'id',
					data:80,
					success(res){
						console.log('获取成功',res.data)
						}
				}) */
				const res = uni.getStorageSync('id')
				console.log(res)
			},
			removeId(){
				/* uni.removeStorage({
					key:'id',
					success(){
						console.log('删除成功')
						}
				}) */
				uni.removeStorageSync('id')
			}
		}
	}
</script>

<style>
	.bos-item{
		width: 100px;
		height: 100px;
	}
</style>
